body{overflow-x:hidden}
.outer-wrapper{display:-webkit-box;display:-ms-flexbox;display:flex;margin:0}
.inner-wrapper{-webkit-box-flex:1;-ms-flex:1 1 auto;flex:1 1 auto;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-flow:column nowrap;flex-flow:column nowrap;display:-webkit-box;display:-ms-flexbox;display:flex;min-height:100vh;-ms-flex-direction:column;flex-direction:column;width:100%;position:relative}
.main-content{z-index:1;z-index:var(--ds-main-z-index);-webkit-box-flex:1;-ms-flex:1 1 100%;flex:1 1 100%;margin-top:1.5rem;margin-top:var(--ds-content-spacing);margin-bottom:1.5rem;margin-bottom:var(--ds-content-spacing)}
.alert.hide{padding:0;margin:0}
ds-admin-sidebar{position:fixed;z-index:20;z-index:var(--ds-sidebar-z-index)}
.sticky-top{z-index:0}
.media-viewer .change-gallery .ngx-gallery ngx-gallery-preview.ngx-gallery-active{right:0;left:auto;width:calc(100% - 53px)}
.ds-submission-reorder-dragging .ds-hint,.ds-submission-reorder-dragging button{display:none}
.facet-filter,.setting-option{background-color:#e8ebf3;background-color:var(--bs-light);border-radius:0.25rem;border-radius:var(--bs-border-radius)}
.facet-filter.p-3,.setting-option.p-3{padding-top:.5rem !important;padding-bottom:.5rem !important}
